import { QueryKey, useMutation, useQuery } from "react-query";
import { User } from "types/user";
import { cleanObject } from "utils";
import { useHttp } from "./http";
import { useAddConfig, useDeleteConfig } from "./use-optimistic-options";

export const useUsers = (param?: Partial<User>) => {
  const client = useHttp();

  return useQuery<User[]>(["users", cleanObject(param)], () =>
    client("users", { data: param })
  );
};
export const useUsersQueryKey = () => {
  return ["users", {}];
};
/**
 * Ê∑ªÂä†È°πÁõÆ
 * @returns mutate--Ë∞ÉÁî®ÂÖ∂Â§ÑÁêÜËØ∑Ê±ÇÔºåÂπ∂‰πêËßÇÊõ¥Êñ∞
 */
export const useAddUser = (queryKey: QueryKey) => {
  const client = useHttp();
  return useMutation(
    (params: Partial<User>) =>
      client(`users`, {
        data: params,
        method: "POST",
      }),
    useAddConfig(queryKey)
  );
};
/**
 * Âà†Èô§È°πÁõÆ
 * @returns mutate--Ë∞ÉÁî®ÂÖ∂Â§ÑÁêÜËØ∑Ê±ÇÔºåÂπ∂‰πêËßÇÊõ¥Êñ∞
 */
export const useDeleteUser = (queryKey: QueryKey) => {
  const client = useHttp();
  return useMutation(
    ({ id }: { id: number }) =>
      client(`users/${id}`, {
        method: "DELETE",
      }),
    useDeleteConfig(queryKey)
  );
};
